<script>
export default {
  data() {
    var self = this;
    return {
      data: "",
      step: 1,
      loading: false,
      dialog: false,
      employee: {},
      education: {},
      experience: {},
    };
  },
  created() {
    this.getCareerProfile();
    this.$store.dispatch("dictionary/getCountries");
  },
  methods: {
    next(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.step++;
        } else {
          return false;
        }
      });
    },
    back() {
      this.step--;
    },
    getCareerProfile() {
      // var profile_id = JSON.parse(localStorage.getItem("profile_id"));
      this.$endpoint.getCareerProfile(this.$store.state.auth.user.id).then(
        (res) => {
          this.loading = false;
          this.employee = res.data.data;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    submit(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.$endpoint.savePersonnel(this.employee).then(
            () => {
              this.loading = false;
              this.step = 2;
            },
            (error) => {
              this.loading = false;
              this.message =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
              this.$notify({
                title: "Error",
                message: this.message,
                type: "error",
              });
            }
          );
        } else {
          return false;
        }
      });
    },
    saveEducation() {
      this.loading = true;
      this.$endpoint.saveEducation(this.education).then(
        () => {
          this.loading = false;
          this.step = 3;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    saveExperience() {
      this.loading = true;
      this.$endpoint.saveExperience(this.experience).then(
        () => {
          this.loading = false;
          this.step = 1;
          this.$notify({
            title: "Success",
            message: "Profile has been updated",
            type: "success",
          });
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },

    async uploadProfilePhoto(event) {
      let file = event.target.files[0];
      await this.$helpers
        .getBase64(file)
        .then((data) => (this.employee.profile_photo = data));

      this.loading = true;
      this.$endpoint.saveUser(this.employee).then(
        () => {
          this.loading = false;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },

    handleFileUpload(e) {
      if (!this.employee.document_type || !this.employee.document_title) {
        this.$notify({
          title: "Error",
          message: "Document title and type are required",
          type: "error",
        });
        return;
      }
      this.loading = true;
      this.file = e.raw;
      let formData = new FormData();
      formData.append("document_type", this.employee.document_type);
      formData.append("document_title", this.employee.document_title);
      formData.append("file", this.file);
      this.$endpoint.saveDocument(formData).then(
        (res) => {
          this.loading = false;
          this.getCareerProfile();
          this.$notify({
            title: "Success",
            message: res.data.message,
            type: "success",
          });
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
